* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'avenir';
}

a {
    text-decoration: none;
}

body {
    background: #ffffff;   

    &.lock {
        overflow: hidden;
    }
}



ul {
    list-style: none;
}

.container {
    @include container();
    height: 100%;
}

.row {
    @include row();
}